.App {
  text-align: center;
}

.App-logo {
  background: #282c34;
  height: 40vmin;
  pointer-events: none;
}
.header-title{
  background: #121212;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: minmax(300px, auto);
  grid-template-areas:
  ". leftlogo hd hd hd hd hd hd hd hd rightlogo ."
}
.container{
  padding:50px;
  background: #121212;
  display: grid;
  grid-template-columns: repeat(6, 2fr);
  grid-template-rows: minmax(800px, auto);
  grid-template-areas:
  "sadlogo sadlogo text-container text-container text-container text-container"
}
.container-2{
  padding:50px;
  background: white;
  display: grid;
  grid-template-columns: repeat(6, 2fr);
  grid-template-rows: minmax(800px, auto);
  grid-template-areas:
  "tc2 tc2 tc2 tc2 happylogo happylogo"
}
.container-3{
  padding:50px;
  background: #121212;
  display: grid;
  grid-template-columns: repeat(6, 2fr);
  grid-template-rows: minmax(800px, auto);
  grid-template-areas:
  "templogo1 tc3 tc3 tc3 tc3 templogo"
}

.text-container{
  grid-area:text-container;
}
.text-container h1{
  padding: 50px;
  font-size:3rem;
}
.text-container-2{
  grid-area:tc2;
  margin-top:10%;
}
.text-container-2 h1{
  padding: 50px;
  font-size:50px;
  color:black;
}
.text-container-3{
  grid-area:tc3;
  margin-top:10%;
}
.text-container-3 h2{
  padding: 50px;
  color:white;
  padding:50px;
}
.templogo{
  grid-area:templogo;
  margin-top: 50%;
}
.templogo1{
  grid-area:templogo1;
  margin-top:50%;
}
.templogo img{
  background: #121212;
  max-width: 500px;
  max-height: 500px;
}
.templogo1 img{
  background: #121212;
  max-width: 500px;
  max-height: 500px;
}
.leftlogo{
  grid-area:leftlogo;
  padding-top:50px;
}
.leftlogo img{
  background: #121212;
  width: auto;
  height: auto;
  max-width: 250px;
  max-height: 250px;
}
.rightlogo{
  grid-area:rightlogo;
  padding-top:50px;
}
.rightlogo img{
  background: #121212;
  width: auto;
  height: auto;
  max-width: 250px;
  max-height: 250px;
}
.sadlogo{
  grid-area:sadlogo;
  margin-top: 20%;
}
.sadlogo img{
  background: #121212;
  max-width: 500px;
  max-height: 500px;
}
.hd{
  grid-area:hd;
  padding-bottom: 50px;
}
.hd h1{
  text-align:center;
  text-decoration: line-through black;
  font-size:6rem;
}
.happylogo{
  grid-area:happylogo;
  margin-top:25%;
}
.happylogo img{
  background: white;
  max-width: 500px;
  max-height: 500px;
}



@media (prefers-reduced-motion: no-preference) {
  .logo-motion{
    animation: App-logo-spin infinite 8s linear;
  }
}

@media (prefers-reduced-motion: no-preference) {
  .happy-logo-motion {
    animation: Happy-logo-spin infinite 20s linear;
  }
}
@media (prefers-reduced-motion: no-preference) {
  .sad-logo-motion {
    animation: Sad-logo-spin infinite 30s linear;
  }
}


.App-header {
  background: green;
  color:white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes Happy-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes Sad-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}